.authNavbar {
    padding: none !important;
    position: relative !important;
    background-color: white !important;
}

.header-body .card-stats {
    height: 100%;
    margin: "20px";
}

.carousel {
    height: calc(100vh - 150px);
    width: 100%;
}

.carouselImage {
    width: 100%;
}

.firstPartText {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 1;
}

.homeButton:hover {
    font-size: 18px;
}

.nav-link-icon {
    color: black !important;
}

.nav-link-icon:hover {
    color: #1063ff !important;
}

.nav-footer a {
    margin: 0 20px;
}

.footer {
    padding: 50px 200px;
}

.nav-link-icon .dropdown-toggle {
    padding: 0 !important;
    box-shadow: none !important;
    background-color: transparent !important;
}

.adminFooterLink span:hover {
    cursor: pointer;
    color: rgb(211, 126, 14) !important;
}

.adminFooterLink img:hover {
    border-radius: 20px;
    box-shadow: 0 0 0 2px orange;
}

.chart {
    height: 100% !important;
}

.bg-dark .pagination > li > a {
    background-color: black;
    color: white;
}

.bg-dark .pagination > li > a:focus,
.bg-dark .pagination > li > a:hover,
.bg-dark .pagination > li > span:focus,
.bg-dark .pagination > li > span:hover {
    color: white;
    background-color: black;
    border-color: black;
}

.bg-dark .pagination > .active > a {
    color: white;
    background-color: #172b4d !important;
    border: solid 1px white !important;
}

.bg-dark .pagination > .active > a:hover {
    background-color: #172b4d !important;
    border: solid 1px #172b4d;
}

.special1 {
    position: absolute;
    background-color: white;
    padding: 10px;
    height: 100%;
    border-radius: 10px;
    display: none;
}

.special2 {
    position: absolute;
    top: 0px;
    background-color: white;
    padding: 10px;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    display: none;
}

nav#w-dropdown-list-0 {
    height: max-content;
}

@media (max-width: 1200px) {
    .carouselImage {
        width: auto;
        height: 100%;
    }

    .firstPartTitle {
        font-size: 30px !important;
    }

    .firstPartSubTitle {
        font-size: 18px !important;
    }

    .secondPartText {
        text-align: center !important;
    }

    .thirdPartText {
        text-align: center !important;
    }

    .fifthPartText {
        text-align: center !important;
    }

    .footer {
        padding: 50px 50px;
    }
}

@media (max-width: 992px) {
    .fourthPartImg {
        width: 100% !important;
    }

    .authNavbarLogoText {
        display: none !important;
    }

    .homeButton {
        font-size: 15px !important;
        padding: 0 10px !important;
    }

    .organizationLogo {
        display: none !important;
    }
}

@media (max-width: 768px) {
    .secondPartText {
        padding: none !important;
    }

    .secondPartTitle {
        font-size: 22px !important;
    }

    .secondPartSubTitle {
        font-size: 22px !important;
    }

    .sixthPartTitle {
        font-size: 50px !important;
    }

    .aboutUsText {
        font-size: 20px !important;
    }

    .authForm {
        width: 100% !important;
    }

    .sideBarLogoText {
        display: none !important;
    }
}

@media (max-width: 576px) {
    .firstPartTitle {
        font-size: 18px !important;
    }

    .firstPartSubTitle {
        font-size: 12px !important;
    }

    .aboutUsText {
        font-size: 16px !important;
    }

    .contactDetails {
        padding: 10px !important;
    }

    .contactForm {
        margin: 20px auto !important;
        width: 100% !important;
    }

    .footer {
        padding: 50px 0px;
    }
}

